.news {
    padding: 96px 0 150px;
    &__inner {
        &-item {
            display: flex;
            flex-wrap: wrap;
            width: 50%;
        }
    }
    &__item {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;
        margin-top: 30px;
        color: var(--darkWhite);
        overflow: hidden;
        &-100 {
            width: 100%;
            padding: 180px 50px 50px;
            time {
                margin-top: 95px;
            }
            p {
                padding-right: 225px;
            }
        }
        &-50 {
            display: flex;
            width: 50%;
            .news__item {
                padding: 171.6px 30px 30px;
            }
            .item__tag-light {
                top: 30px;
                left: 30px;
            }
            p {
                height: 88px;
                overflow: hidden;
            }
            h3 {
                margin-right: -15px;
            }
            time {
                margin-top: 74px;
            }
        }
        &-bg, &::after {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
        }
        &-bg {
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
            transition: all .3s;
        }
        &::after {
            content: "";
            background-color: rgba(50, 50, 50, .78);
        }
        .typography {
            position: relative;
            z-index: 2;
            a {
                &::after {
                    position: absolute;
                    left: -100px;
                    top: -200px;
                    height: calc(100% + 500px);
                    width: calc(100% + 500px);
                    background-color: transparent;
                }
                &:hover {
                    color: var(--White);
                }
            }
        }
        time {
            position: relative;
            z-index: 1;
            display: flex;
            align-items: center;
            white-space: nowrap;
            font-size: 14px;
            i {
                margin-right: 10px;
            }
        }
        &:hover {
            .news__item-bg {
                transform: scale(1.2);
            }
        }
    }
}
@media(max-width: 1600px) {
    .news {
        &__item {
            &-100 {
                padding: 180px 30px 30px;
                p {
                    padding: 0;
                }
                .item__tag-light {
                    top: 30px;
                    left: 30px;
                }
            }
            &-50 {
                .news__item {
                    padding: 170px 20px 20px;
                }
                .item__tag-light {
                    top: 20px;
                    left: 20px;
                }
            }
        }
    }
}
@media(max-width: 1300px) {
    .news {
        &__inner {
            flex-wrap: wrap;
            &-item {
                width: 100%;
            }
        }
        &__item {
            padding: 140px 20px 20px;
            .item__tag-light {
                top: 20px;
                left: 20px;
            }
            &-50 {
                .news__item {
                    padding: 140px 20px 20px;
                }
            }
        }
    }
}
@media(max-width: 900px) {
    .news {
        padding: 60px 0 120px;
    }
}
@media(max-width: 767px) {
    .news {
        &__inner {
            .flexRow {
                flex-wrap: wrap;
            }
        }
        &__item {
            &-50 {
                width: 100%;
            }
            time {
                margin-top: 60px;
            }
            p {
                height: 88px;
                overflow: hidden;
            }
            h3 {
                margin-right: 0;
            }
        }
    }
}